<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :model="model"
        icon="mdi-desktop-tower-monitor"
        :entity-name="$t('分区管理')"
      />
      <v-card>
        <v-toolbar
          dense
          color="primary"
          dark
        >
          <v-toolbar-title>{{ $t('分区分布') }}</v-toolbar-title>
          <v-spacer/>
        </v-toolbar>
        <v-card-text>
          <v-data-table
            dense
            hide-default-footer
            :headers="headers"
            :items-per-page="100"
            :items="tableData"
          >
            <template
              v-for="(s, idx) in sectionHeaders"
              v-slot:[`item.${s.name}`]="{ item }"
            >
              <v-checkbox
                :key="idx"
                :disabled="loading"
                :input-value="item[s.name]"
                @click="updateSection(item, s)"
              />
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>

  import Section from '../../model/Section'
  import Servant from '../../model/Servant'
  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'
  import hillo from 'hillo'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: Section,
        displayData: [],
        loading: false,
        fixedHeader: [
          {
            text: 'name',
            value: 'name',
          },
          {
            text: 'password',
            value: 'password',
          },
        ],
        sectionHeaders: [],
      })
    },
    computed: {
      headers () {
        return this.fixedHeader.concat(this.sectionHeaders)
      },
      tableData () {
        const tableData = []
        this.displayData.forEach(d => {
          this.sectionHeaders.forEach(s => {
            s.copyServantsIds = IKUtils.deepCopy(s.servantIds)
            if (d.id) {
              if ((s.servantIds !== null) && (s.servantIds.length > 0)) {
                d[s.name] = (s.servantIds.indexOf(d.id) > -1)
              } else {
                d[s.name] = false
              }
            }
          })
          tableData.push(d)
        })
        console.log(tableData, 'item')
        return tableData
      },
    },
    mounted () {
      this.loadData()
    },
    methods: {
      async updateSection (item, sItem) {
        this.loading = false
        item[sItem.name] = !item[sItem.name]
        if (typeof (sItem.copyServantsIds) === 'string') {
          sItem.copyServantsIds = JSON.parse(sItem.copyServantsIds)
        }
        if (item[sItem.name]) {
          sItem.copyServantsIds.push(item.id)
        } else {
          sItem.copyServantsIds.splice(sItem.copyServantsIds.indexOf(item.id), 1)
        }
        sItem.servantIds = JSON.stringify(sItem.copyServantsIds)
        await hillo.post('Section.php?op=update', sItem)
        // try {
        //   await hillo.post('Section.php?op=update', sItem)
        //   item[sItem.name] = !item[sItem.name]
        //   this.loading = false
        // } catch (e) {
        //   console.log(e, 'e')
        //   this.loading = false
        // }
        // this.loadData()
      },
      async loadData () {
        this.sectionHeaders = (await Section.getList()).map(a => {
          a.text = a.name
          a.value = a.name
          return a
        })
        this.displayData = (await Servant.getList())
      },
    },
  }
</script>
